/* ==========================================================================
   Tabs shortcode responsive style - begin
   ========================================================================== */

@include laptop-landscape {

	.edgtf-tabs {

		&.edgtf-tabs-standard,
		&.edgtf-tabs-standard-reverse,
		&.edgtf-tabs-boxed {

			.edgtf-tabs-nav {

				li {

					a {
						margin: 0 25px;
					}
				}
			}
		}
	}
}

@include ipad-landscape {
	
	.edgtf-tabs {
		
		&.edgtf-tabs-standard,
		&.edgtf-tabs-standard-reverse,
		&.edgtf-tabs-boxed {

			.edgtf-tabs-nav {
				
				li {
					
					a {
						padding: 7px 21px;
					}
				}
			}
		}

		&.edgtf-tabs-simple {
			
			.edgtf-tabs-nav {
				
				li {
					margin: 0 26px 0 0;
				}
			}
		}
	}
}

@include ipad-portrait {
	
	.edgtf-tabs {
		
		&.edgtf-tabs-standard,
		&.edgtf-tabs-standard-reverse,
		&.edgtf-tabs-boxed {
			
			.edgtf-tabs-nav {
				
				li {
					display: block;
					float: none;
					
					a {
						width: 100%;
						margin: 0;
					}
				}
			}
		}

		&.edgtf-tabs-simple {
			
			.edgtf-tabs-nav {
				
				li {
					margin: 0 20px 0 0;
				}
			}
		}
	}
}

@include phone-landscape {
	
	.edgtf-tabs {
		
		&.edgtf-tabs-simple {
			
			.edgtf-tabs-nav {
				padding: 0 0 20px;
				
				li {
					display: block;
					float: none;
					margin: 0 0 20px;
					
					a {
						padding: 0;
						width: 100%;
					}
				}
			}
		}
	}
}
/* ==========================================================================
   Tabs shortcode responsive style - end
   ========================================================================== */